.products_,
.products_ .card.border-none{
    min-height: calc(100vh - 90px);
}

.products_ .table.table-lg > :not(caption) > * > *{
    border-style: dashed;
}

.tb_pro__image_wrap {
    position: relative;
    width: 150px;
    height: 150px;
    margin: 0 auto;
    margin-bottom: 15px;
}

.tb_pro__image {
    height: 100%;
    left: 0;
    object-fit: contain;
    object-position: center;
    position: absolute;
    top: 0;
    width: 100%;
}

.tb_pro__title {
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    color: #2b2b2b;
    display: -webkit-box;
    font-size: 14px;
    margin-bottom: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    text-align: center;
    word-break: break-word;
}

.tb_pro__price, .tb_pro__price_wrap {
    align-items: center;
    display: flex;
    justify-content: center;
}
.tb_pro__price_wrap {
    flex-wrap: wrap;
}

.tb_pro__discount_price, .tb_pro__price {
    color: #2b2b2b;
    font-size: 16px;
}

.tb_pro__discount_price {
    opacity: .7;
    text-decoration: line-through;
    transform: scale(.75);
}

.tb_pro__link {
    align-items: center;
    display: flex;
    justify-content: center;
    margin-top: auto;
    padding-top: 10px;
    width: 100%;
}

.tb_pro__btn {
    background-color: #545454;
    color: #fff;
    cursor: pointer !important;
    display: inline-block;
    font-size: 14px;
    letter-spacing: 1px;
    max-width: 100%;
    overflow: hidden;
    padding: 8px 20px;
    text-overflow: ellipsis;
    vertical-align: top;
}

.addPro_modal .selectpicker__control{
    border: none !important;
}
@media (min-width: 991px) {
.sync_products .gx-3 .col-6{
        width: 20%;
}
.sync_products .card{
    height: 200px;
}

}
.syncPro_modal_wrapper{
    display: flex;
    align-items: flex-start;
}

.signInWithBrand {
    background-color: var(--bs-light);
    border-radius: 2px;
}

#syncProducts .signInWithBrand {
    padding: 1px;
    width: 180px;
}

.signIn_txt{
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    width: auto;
    flex-shrink: 0;
}

.signInWithBrandIcon{
    background-color: var(--bs-gray-200);
    border-radius: 2px;
    justify-content: center;
}

.signInWithBrandIcon img {
    height: 20px;
    width: 20px;
}

.syncPro_modal_wrapper > div:first-child {
    width: 150px;
}

.signIn-Google {
    background-color: #4285f4;
}
.signIn-Google:hover,
.signIn-Google:focus,
.btn.signIn-Google:active,
.btn.signIn-Google:first-child:active{
    background-color: #4285f4;
    box-shadow: 0 0 5px #4285f4;
}

