    box-shadow: 0 0 5px #7f54b2;
}

.syncPro_modal {
    padding-top: 10%;
    padding-bottom: 10%;
}

[aria-labelledby="syncProducts"] + .modal-backdrop.show{
    z-index: 1055;
}

@media (max-width: 991px) {
    .syncPro_modal {
        padding-top: 2%;
        padding-bottom: 2%;
    }
}

@media (max-width: 575px) {
    .products_ .card-header .btn,
    .products_ .card-header .d-flex{
        width: 100%;
    }
    .syncPro_modal_wrapper{
        width: 100%;
        flex-direction: column;
        align-items: stretch;
        border-bottom: 1px solid var(--bs-border-color);
        padding-bottom: 15px;
    }
    .syncPro_modal_wrapper:last-child{
        border-bottom: none;
    }

    .syncPro_modal {
        padding-top: 0;
        padding-bottom: 0;
    }
}